import React, { useEffect } from "react";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import InboxIcon from "@material-ui/icons/MoveToInbox";
import DraftsIcon from "@material-ui/icons/Drafts";
import SendIcon from "@material-ui/icons/Send";
import MenuIcon from "@material-ui/icons/Menu";
import { Link, useHistory } from "react-router-dom";
import { useCurrentUser } from "../../hooks/auth/useCurrentUser";
import { useAppContext } from '../../store/AppContext';
import "../index.css";
import { useUserContext } from "../../store/UserContext";
import LocalStorageService from "../../utils/localStorageService";

const localStorageService = LocalStorageService.getService();

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    height: '100vh',
    display: 'flex',
    flexDirection: 'column'
  },
  title: {
    flexGrow: 1,    
    textAlign: 'center'
  },
  menu: {
    display: 'flex',
    alignItems: 'center',
  },
  first: {
    color: 'white',
    textTransform: 'uppercase'
  },
  chat: {
    paddingLeft: 20,
    color: 'white'
  }
}));
const StyledMenu = withStyles({
  paper: {
    border: "1px solid #d3d4d5",
  },
})((props) => (
  <Menu
    elevation={0}
    getContentAnchorEl={null}
    anchorOrigin={{
      vertical: "bottom",
      horizontal: "center",
    }}
    transformOrigin={{
      vertical: "top",
      horizontal: "center",
    }}
    {...props}
  />
));

const StyledMenuItem = withStyles((theme) => ({
  root: {
    "&:focus": {
      backgroundColor: theme.palette.primary.main,
      "& .MuiListItemIcon-root, & .MuiListItemText-primary": {
        color: theme.palette.common.white,
      },
    },
  },
}))(MenuItem);
export default function ButtonAppBar({ children }) {
  const classes = useStyles();
  const history = useHistory();
  const { authData, setAuthData  } = useUserContext();
  const current = null;
  const { data: currentUser } = useCurrentUser();
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6" className={classes.title}>
            <Typography variant="span"><Link className={classes.first} to="/">Robopro</Link></Typography>
            <Typography variant="span"><Link className={classes.chat} to="/chat"></Link></Typography>
          </Typography>
          {authData ? (
            <div className={classes.menu}>              
              <button
                className="menu-button"
                aria-haspopup="true"
                variant="contained"
                onClick={handleClick}
              >
                {authData && (
                  <>
                    {currentUser?.profile.avatar != null ? (
                      <div
                        className="menu-avater"
                        style={{
                          backgroundImage: `url(${currentUser?.profile.avatar})`,
                        }}
                      ></div>
                    ) : (
                      <div
                        className="menu-avater"
                        style={{
                          backgroundImage: `url(data:image/jpeg;base64,)`,
                        }}
                      ></div>
                    )}
                    
                  </>
                )}
              </button>
              <StyledMenu
                id="customized-menu"
                anchorEl={anchorEl}
                keepMounted
                open={Boolean(anchorEl)}
                onClose={handleClose}
              >                
                {currentUser?.is_superuser == true ? (
                  <StyledMenuItem>
                    <Link to="/users">
                      <Typography variant="h6"></Typography>{" "}
                    </Link>
                  </StyledMenuItem>
                ) : (
                  ""
                )}
                <StyledMenuItem>
                  <Link to="/familypage">
                    <Typography variant="h6"></Typography>{" "}
                  </Link>
                </StyledMenuItem>
                <StyledMenuItem>
                  <Link to="/profile">
                    <Typography variant="h6"></Typography>{" "}
                  </Link>
                </StyledMenuItem>
                {!authData && (
                  <>
                    <StyledMenuItem>
                      <Link to="/login">
                        <Typography variant="h6"></Typography>{" "}
                      </Link>
                    </StyledMenuItem>
                    <StyledMenuItem>
                      <Link to="/signup">
                        <Typography variant="h6"></Typography>{" "}
                      </Link>
                    </StyledMenuItem>
                  </>
                )}

                {authData && (
                  <StyledMenuItem>
                    <Link
                      onClick={() => {
                        localStorageService.clearToken(null);
                        setAuthData(null);
                        history.push("/login");
                      }}
                    >
                      <Typography variant="h6"></Typography>{" "}
                    </Link>
                  </StyledMenuItem>
                )}
              </StyledMenu>
            </div>
          ) : (
            <>
              {" "}
              <Box mx={2}
                style={{
                  position: 'absolute',
                  right: '65px',
                }}
              >
                <Link to="/login">
                  {" "}
                  <Typography variant="h6"></Typography>{" "}
                </Link>
              </Box>
              <Box mx={2}
                style={{
                  position: 'absolute',
                  right: '5px',
                }}
              >
                <Link to="/signup">
                  {" "}
                  <Typography variant="h6"></Typography>{" "}
                </Link>
              </Box>{" "}
            </>
          )}
        </Toolbar>
      </AppBar>
      {children}
    </div>
  );
}
